<script>
import modelRule from '../components/rule'
import config from '@/config'
// const desiredNetwork = "4"; // 网络监测
console.debug(config.desiredNetwork);
export default {
  name: "ethHeader",

  data() {
    return {
      user_name: '',
      sysAppIds:window.location.href,
      isShowRule:false
    };
  },

  components: {
    // 模板
    modelRule
  },

  computed: {
    // 计算属性
    userNames() {
      if(this.user_name == ''){
        this.handleLogin() 
        return ''
        }
      let str = this.user_name.slice(0,4) +'...'+this.user_name.slice(this.user_name.length-4) 
      return str ||this.$t("login");
    }
  },


  methods: {
    onCopy(){
        // alert(33)
        this.$Message.success(this.$t('copy'));
    },
    onRule() {
      this.isShowRule = ! this.isShowRule
    },
    // 方法
    handelClickMenuitem(e) {
      // alert(e)
    },
    handelLang() {
      // console.debug(this.$i18n.locale);
      if (this.$i18n.locale == "zh-CN") {
        this.$i18n.locale = "en-US";
      } else {
        this.$i18n.locale = "zh-CN";
      }
    },
    handleLogin() {
      let _self = this;
      // alert('1111')
      if (typeof window.ethereum === "undefined") {
        alert("Looks like you need a Dapp browser to get started.");
        alert("Consider installing MetaMask!");
      } else {
        // In the case the user has MetaMask installed, you can easily
        // ask them to sign in and reveal their accounts:
        ethereum
          .enable()

          // 记住处理他们拒绝请求的案例：
          .catch(function(reason) {
            if (reason === "User rejected provider access") {
              // 用户拒绝访问程序
              // 用户不想登录
            _self.handleLogin()
            } else {
              // This shouldn't happen, so you might want to log this...
              _self.handleLogin()
            }
          })

          // 如果他们批准了登录请求，您将收到他们的帐户：
          .then(function(accounts) {
            console.debug(ethereum);
            // 您还应验证用户是否在正确的网络上：
            if (ethereum.networkVersion !== config.desiredNetwork) {
              alert("此应用程序需要主网络，请在MetaMask用户界面中进行切换 UI.");

              // We plan to provide an API to make this request in the near future.
              // https://github.com/MetaMask/metamask-extension/issues/3663
            }

            // 一旦您有了对用户帐户的引用，
            // 您可以建议交易和签名：
            const account = accounts[0];
            console.debug(account);
            _self.user_name = account;
          });
      }
    }
  },

  // beforeCreate:function(){
  //     console.debug('1-beforeCreate 初始化之后');
  // },
  created:function(){
      // console.debug('2-created 创建完成');
       this.handleLogin()
    this.userNamesTime = setInterval(() => {
        // this.handleLogin();
        this.user_name = window.web3.currentProvider.selectedAddress
      },10000)
  },
  // beforeMount:function(){
  //     console.debug('3-beforeMount 挂载之前');
  // },
  // mounted:function(){
  //     console.debug('4-mounted 被创建');
  // },
  // beforeUpdate:function(){
  //     console.debug('5-beforeUpdate 数据更新前');
  // },
  // updated:function(){
  //     console.debug('6-updated 被更新后');
  // },
  // activated:function(){
  //     console.debug('7-activated');
  // },
  // deactivated:function(){
  //     console.debug('8-deactivated');
  // },
  // beforeDestroy:function(){
  //     console.debug('9-beforeDestroy 销毁之前');
  // },
  destroyed:function(){
      // console.debug('10-destroyed 销毁之后')
      clearInterval(this.userNamesTime)
  }
};
</script>
